<template>
  <div
    class="ex-breadcrumb"
    aria-label="Breadcrumb"
    role="navigation"
  >
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'ExBreadcrumb',
  props: {
    // 
    separator: {
      type: String,
      default: '/'
    },
    // 
    separatorIcon: {
      type: String,
      defualt: ''
    }
  },
  provide() {
    return {
      ExBreadcrumb: this
    }
  },
  mounted() {
    // 
    const el = this.$el
    // 
    const listEL = el.querySelectorAll('ex-breadcrumb--item')
    if (listEL.length) {
      //  TODO  
      listEL[listEL.length - 1].setAttribute('aria-current', 'page')
    }
  }
}
</script>