<template>
  <transition name="ex-fade-in">
    <div
      v-if="visible"
      class="ex-backtop"
      @click.stop="onClick"
      :style="{right: right+ 'px', bottom: bottom + 'px'}"
    >
      <!--  -->
      <slot>
        <!--   -->
        <ex-icon type="caret-top"></ex-icon>
      </slot>
    </div>
  </transition>
</template>
<script>
// 
import { throttle } from 'throttle-debounce'

export default {
  name: 'ExBacktop',
  props: {
    right: {
      type: Number,
      default: 20
    },
    bottom: {
      type: Number,
      default: 20
    },
    // 200
    visibilityHeight: {
      type: Number,
      default: 200
    },
    // 
    traget: [String]
  },
  data() {
    return {
      visible: false,
      // 
      container: null,
      // 
      el: null
    }
  },
  mounted() {
    this.init()
    this.scrollHandler = throttle(300, this.onScroll)
    // 
    this.container.addEventListener('scroll', this.scrollHandler)
  },
  beforeDestroy() {
    this.container.removeEventListener('scroll', this.scrollHandler)
  },
  methods: {
    init() {
      //  document
      this.container = document
      // 
      this.el = document.documentElement

      // 
      if (this.target) {
        this.el = document.querySelector(this.target)
        if (!this.el) {
          throw new Error('target ')
        }
        this.container = this.el
      }
    },
    // 
    onClick(e) {
      this.onScrollToTop()
      this.$emit('click', e)
    },
    // 
    onScrollToTop() {
      const el = this.el
      const beginTime = Date.now()
      // 
      const scrollTop = el.scrollTop
      // 
      const cubic = value => Math.pow(value, 3)
      const easeInOutCubic = value => value < 0.5 ? cubic(value * 2) / 2 : 1 - cubic((1 - value) * 2) / 2
      // 
      const animate = window.requestAnimationFrame || (func => setTimeout(func, 16))
      //  TODO
      const frameFunc = () => {
        const progress = (Date.now() - beginTime) / 500
        if (progress < 1) {
          el.scrollTop = scrollTop * (1 - easeInOutCubic(progress))
          animate(frameFunc)
        } else {
          el.scrollTop = 0
        }
      }

      // 
      animate(frameFunc)
    },
    // 
    onScroll() {
      const scrollTop = this.el.scrollTop
      //  visibilityHeight 
      this.visible = scrollTop >= this.visibilityHeight
    }
  }
}
</script>